body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: "Kotra";
    font-display: fallback;

    src: url(/wedding/static/media/KOTRA_GOTHIC.00b8334c.ttf) format("truetype"), url(/wedding/static/media/KOTRA_GOTHIC.312dbcd2.woff)
    format("woff"), url(/wedding/static/media/KOTRA_GOTHIC.612aba56.eot) format("embedded-opentype");

    /* 사용할 폰트의 기본 스타일 */
    font-weight: 400;
    font-style: normal;
}

